import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { HttpHeaders } from '@angular/common/http';
import { Observable } from 'rxjs/Observable';
import * as jwt from 'jsonwebtoken';
import { FlashMessagesService } from 'angular2-flash-messages/module/flash-messages.service';
import { Router, RouterModule } from '@angular/router';


export class Post {
  postid: number;
  created: Date;
  modified: Date;
  title: string;
  body: string;
}
const httpOptions = {
  headers: new HttpHeaders({
    'Content-Type':  'application/json'
  }),
  withCredentials: true,
  responseType: 'text' as 'text'
};

let updateFail: boolean = false;
let postFail: boolean = false;
let deleteFail: boolean = false;


@Injectable()
export class BlogService {

  private posts: Post[] = [];
  PostCount : number = 1;
  PostCountName : string = "#PostCount";
  PostBaseName : string = "#Post";
  constructor(private http: HttpClient, private flash: FlashMessagesService, private router: Router) { 
    this.fetchPosts();
  }
  private postToString (post: Post) : string
  {
    return JSON.stringify(post);
  }
  private stringToPost(s: string) : Post
  {
    return JSON.parse(s)
  }
  fetchPostsCallBack(data : Post[]) : void
  {
    data.forEach(x => this.posts.push(x));
    this.PostCount = this.posts[this.posts.length - 1].postid + 1;
  }

  fetchPosts(): void
  {
    this.http.get<Post[]>('http://localhost:3000/api/'+ this.getUsername())
    .subscribe(data => this.fetchPostsCallBack(data))
  }
  getPosts(): Post[] 
  {
    return this.posts;
  }
  getPost(id: number) : Post
  {
    return this.posts.filter(x => x.postid == id)[0];
  }
  getUsername() : string{
    var cookie = document.cookie;
    cookie = cookie.substr(cookie.indexOf("=") + 1);
    console.log(cookie);
    var decodedCookie = jwt.decode(cookie, { complete: true });
    // console.log('Cookie Header: ' + JSON.stringify(decodedCookie.header));
    // console.log('Cookie Payload: ' + JSON.stringify(decodedCookie.payload));
    // console.log('We think username is: ' + decodedCookie.payload.usr);
    return decodedCookie.payload.usr;
  }
  newPost(): Post 
  {
    var p = new Post();
    p.title = 'Title';
    p.body = 'Body';
    p.modified = new Date(Date.now());
    p.created = new Date(Date.now());
    p.postid = this.PostCount;

    //clear time format issues
    p = this.stringToPost(this.postToString(p));
    this.posts.push(p);
    
    let body = JSON.stringify(p);
    console.log(body);
    this.PostCount++;
    this.http.post('http://localhost:3000/api/' + this.getUsername() + '/'+ String(p.postid), body, httpOptions).
    subscribe(response => {
      console.log(response);
      if (response != "Created"){
        this.flash.show('Failed to create post', { timeout: 1000});
        this.router.navigate(['http://localhost:3000/api/'+ this.getUsername()]);
      }
    });
    ;
    return p;
  }
  updatePost(post: Post): void 
  {

    post.modified = new Date(Date.now());
    this.http.put('http://localhost:3000/api/' + this.getUsername() + '/' + String(post.postid), post, httpOptions)
    .subscribe(response => {
      console.log(response);
      if (response.toString() != "OK"){
        this.flash.show('Failed to update post', { timeout: 1000});
        this.router.navigate(['http://localhost:3000/api/' + this.getUsername() + '/' + String(post.postid)]);
      }
    });
  }
  deletePost(postid: number): void
  {
    this.http.delete('http://localhost:3000/api/' + this.getUsername() + '/'+ String(postid))
    .subscribe(response => {
      console.log(response.toString());
      if(response != "OK"){
        this.router.navigate(['http://localhost:3000/api/'+ this.getUsername()]);
        this.flash.show('Failed to delete post', { timeout: 1000} );
      }
    });
    this.posts.splice(this.posts.findIndex(x => x.postid == postid), 1);
  }
}





