import { Component } from '@angular/core';
import { Post } from './blog.service';

@Component({
  selector: 'app-root',
  templateUrl: './app.component.html',
  styleUrls: ['./app.component.css']
})
export class AppComponent {
  currentPost : Post;
  posts : Post[];
  title = 'app';

  constructor()
  {
    this.currentPost = {
      postid: 1,
      created: new Date(),
      modified: new Date(),
      title: 'title',
      body: 'body'
    };
  }
}



